.upload-container {
  display: flex;
  align-items: flex-start;
  height: 100%;
  flex-direction: column;
  position: relative;
}

.upload-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #7E7E7E;
  border-radius: 4px;
  width: 100%;
  height: 54px;
  gap: 16px;
  position: relative;
}

.label {
  position: relative;
  height: 100%;
  width: 83px;
  border-width: 1px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-style: solid;
  border-color: var(--light-grey-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.placeholder {
  color: #7E7E7E;
  width: calc(100% - 99px);
  position: relative;
  border: 1px solid #7E7E7E;
  height: 54px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-left: none;
  display: flex;
  align-items: center;
  z-index: 0;
  line-break: anywhere;
  padding-right: 16px;
}

.filled {
  color: #000000DE;
  /* white-space: nowrap; */
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 100%;
}

.error-field {
  position: relative;
  z-index: 0;
}

.upload-label.error-field::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 54px;
  top: -1px;
  left: -1px;
  border: 2px solid #CB3D40;
  border-radius: 4px;
  z-index: 3;
}

.label.error-field::after {
  position: absolute;
  content: '';
  width: 83px;
  height: 53px;
  top: -1px;
  left: -1px;
  border: 2px solid #CB3D40;
  border-radius: 4px 0 0 4px;
  z-index: 3;
}

@media (max-width: 412px) {
  .upload-container {
    display: flex;
    align-items: center;
    height: 100%;
    flex-direction: column;
    position: relative;
    max-width: 90%;
  }

  .upload-label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #7E7E7E;
    border-radius: 4px;
    width: 320px;
    height: 54px;
    gap: 16px;
    position: relative;
  }

  .label {
    position: relative;
