    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    border-style: solid;
    border-color: var(--light-grey-color);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .placeholder {
    color: #7E7E7E;
    width: calc(100% - 99px);
    position: relative;
    border: 1px solid #7E7E7E;
    height: 54px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-left: none;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 0;
    line-break: anywhere;
    padding-right: 16px;
  }

  .error-field {
    position: relative;
    z-index: 0;
  }

  .upload-label.error-field::after {
    position: absolute;
    content: '';
    width: 320px;
    height: 54px;
    top: -1px;
    left: -1px;
    border: 2px solid #CB3D40;
    border-radius: 4px;
    z-index: 3;
  }

  .label.error-field::after {
    position: absolute;
    content: '';
    width: 83px;
    height: 53px;
    top: -1px;
    left: -1px;
    border: 2px solid #CB3D40;
    border-radius: 4px 0 0 4px;
    z-index: 3;
  }
}
